export const toCamelCase = (str) => {
    return str
        .replace(/(?:^\w|[A-Z]|\b\w)/g, (word, index) => {
            return index === 0 ? word.toLowerCase() : word.toUpperCase();
        })
        .replace(/\s+/g, '');
};

export const currencyFormatter = new Intl.NumberFormat(undefined, {
    style: 'currency',
    currency: 'VND',
});
